import React, { useState } from "react";
import { Image } from "antd";

const ComView = (props, ref) => {
  const { photoWidth, photoHeight, src, style = {} } = props
  const [hasErr, setHasErr] = useState()
  const handleError = e => {
    setHasErr('data:image/png;base64,')
  }
  return (
    <Image
      // width={photoWidth || 50}
      src={hasErr ? 'error' : src}
      onError={handleError}
      style={{
        width: 'auto',
        maxWidth: photoWidth || 50,
        maxHeight: photoHeight || photoWidth || 50,
        ...style
      }}
      fallback={hasErr}
    />
  );
}
export default ComView